import logo from './logo.svg';
import './App.css';

function Home({ onStart }) {
  return (
    <div className="Home fadein">
      <div className="home-title">TÍTULO</div>
      <br />
      <div className="home-desc-cont" style={{ width: 560 }}>
        <div className="home-desc">
          Título es un video interactivo para explorar los recuerdos y las
          vivencias en distintos lugares de nuestra infancia.
          <br />
          <br />
          Responde &nbsp;las &nbsp;preguntas&nbsp; para&nbsp;
          llegar&nbsp;al&nbsp; final.
        </div>
      </div>

      <button onClick={onStart} className="start-button" style={{}}>
        Comenzar
      </button>
    </div>
  );
}

export default Home;
